.readme {
	position: relative;
	cursor: pointer;
	display: flex;
	align-items: flex-end;
	height: 24px;
	line-height: 24px;
	color: var(--text);
	width: 100%;
	height: 24px;
	padding-left: 55px;
}
.readme:hover {
	background-color: var(--tab-active);
}

.readme:after {
	content: "";
	position: absolute;
	top: 2px;
	left: 30px;
	height: 20px;
	width: 20px;
	background: url("../../assets/icons/markdown.svg") center/contain no-repeat;
}
.active {
	outline: 1px solid var(--color-active);
	background-color: var(--bg);
}
